  body {
    height: 100%;
    font-family: 'Lato', sans-serif;
    background-color: #000;
  }
  
  .page-container {
  
  }
  
  .bg-container {
    position: fixed;
    z-index: -100;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    display: flex;
    flex-direction: row;
  }
  
  .bg-left{
    height: 100vh;
    width: 100vh;
    background-size: cover;
    background-position: center;
    background-image: url("img/cropbg2.jpg");
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
  }
  
  .bg-right {
    height: 100vh;
    flex-grow: 1;
    background-size: contain;
    background-image: url("img/slicebg2.jpg");
    -webkit-transition: opacity 1s ease-in-out;
    -moz-transition: opacity 1s ease-in-out;
    transition: opacity 1s ease-in-out;
  }
  
  .content-container {
    display: flex;
  }
  
  .content-left {
    flex-grow: 1;
  }
  
  .content-right {
    max-width: 45em;
    background: rgba(0,0,0,0.6);
    border-left: solid white .1em;
    padding-top: 2em;
    padding-bottom: 5em;
    padding-right: 5em;
    padding-left: 2em;
    color: white;
    height: 100%;
  }
  
  .logo {
    float: right;
    width: 13em;
    margin-top: 2em;
    padding-right: 2em;
    padding-top: .5em;
    padding-bottom: .5em;
    /*background: rgba(255,255,255,0.2);*/
  }
  
  .logo-right {
    display: none;
    margin-top: 0;
  }
  .header-pad {
    flex-grow: 1;
  }
  
  .header-container {
    width: 100%;
    display: flex;
    justify-content: flex-end;
    padding-bottom: 2em;
  }
  .header-item {
    padding: 1em;
  }
  .social {
    width: 2em;
  }
  a.medium-link {
    padding: 0.1em;
  }
  .medium-link img {
    width: 1em;
  }
  
  .social:hover{
    filter:brightness(0.5) sepia(1) saturate(10000%) hue-rotate(145deg)brightness(2);
  }
  
  
  a {
    color: #1cc964;
  }
  
  .front-para {
    padding-bottom: 1em;
  }
  
  .front-para span {
    /*color: #1cc964;*/
  }
   .front-para div {
     font-size: 1.15em;
     padding-bottom: 1em;
     text-align: justify;
   }
  
  .front-para img {
    display: block;
    margin: 0 auto;
    width: 70%;
  
  }
  
  /* ==========================================================================
     Section header styles.
     ========================================================================== */
  
  .section-header {
    display: flex;
    flex-direction: column;
    padding-bottom: 2em;
  }
  
  .sh-text {
    font-size: 3.5em;
  }
  .sub-header .sh-text {
    font-size: 2em;
  }
  
  .sh-topline {
    height: 1em;
    width: 100%;
    border-left: solid #1cc964 5em;
  }
  
  .sh-bottomline {
    height: 1em;
    width: 19.5em; /* hack: we'll adapt the width directly on the HMTL */
    border-right: solid #1cc964 7em;
  }
  
  
  /* ==========================================================================
     A very simple list style for the roadmap.
     Using green squares and
     ========================================================================== */
  
  .rm-container {
    width: 50%;
    margin: auto;
    display: block;
    position: relative;
  }
  
  .rm-container ul {
    margin: 2.5em 0;
    padding: 0;
    display: inline-block;
    margin-bottom: 0px;
  }
  
  .rm-container ul li {
    list-style: none;
    margin: auto;
    margin-left: 0em;
    min-height: 50px;
    border-left: 2px solid #9ea2a4;
    padding: 0 0 10px 30px;
    position: relative;
  }
  
  .rm-container ul li:last-child {
    border-left: 0;
     left: 1px;
  }
  
  .rm-container ul li::before {
    position: absolute;
    left: -11px;
    top: 0px;
    content: " ";
    border: 0px;
    background: #1cc964;
    height: 20px;
    width: 20px;
  }
  
  ul li .title {
    color: #1cc964;
    position: relative;
    font-size: 1em;
  }
  
  ul li .element {
    color: #fff;
    font-size: 0.8em;
  }
  
  ul li .gray {
    color: #9ea2a4;
    font-size: 0.8em;
  }
  
  
  /* ==========================================================================
     Style for stats.
     ========================================================================== */
  
  .stats-row {
    text-align: center;
  }
  
  .stat {
    width: 150px;
    height: 150px;
    text-align: center;
    display: inline-block;
    color: #1cc964;
  }
  
  .stat-number{
    font-size: 60px;
    color: #1cc964;
    display: inline-block;
    text-align: center;
    border-bottom: 30px solid #101e26;
    margin-bottom: -10px;
    line-height: 20px;
    width: 125px;
  }
  
  .stat-text{
    color: #1cc964;
    text-align: center;
  }
  
  
  /* ==========================================================================
     Style for the countdown to the minting.
     ========================================================================== */
  
  .countdown-time {
    padding-top: 3em;
    text-align: center;
    color: #1cc964;
  }
  
  .cd-block {
    width: 130px;
    height: 130px;
    text-align: center;
    display: inline-block;
    color: #1cc964;
  }
  
  .cd-number{
    font-size: 3em;
    color: #1cc964;
    display: inline-block;
    text-align: center;
    border-bottom: 20px solid #101e26;
    margin-bottom: -10px;
    line-height: 20px;
    width: 125px;
  }
  
  .cd-unit{
    color: #1cc964;
    text-align: center;
  }
  
  .cd-sep {
    color: #9ea2a4;
    font-size: 3em;
    top: -10px;
    position: relative;
  }
  
  
  /* ==========================================================================
     End of page watermark
     ========================================================================== */
  
  .watermark {
    width: 100%;
    height: 550px;
    margin-left: -2em;
    background-image: url("img/watermark.png");
    background-position: left;
    background-repeat: no-repeat;
    opacity: 0.45;
    margin-bottom: 0em;
  }
  
  .faqq {
    color: #1cc964;
  }
  
  
  
  /* ==========================================================================
     Big discord button
     ========================================================================== */
  
  .button-container{
    text-align: center;
    margin-top: -30px;
    margin-bottom: 80px;
  }
  
  .green-button{
    background-color: #1cc964;
    color: white;
    border: 0px;
    font-size: 1.5em;
    letter-spacing: 2px;
    padding: 25px 60px;
    cursor: pointer;
    display: inline-block;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    transition: all 0.4s;
    text-decoration: none;
  }
  
  .green-button:hover{
    background-color: orange;
    color: white;
    -webkit-transition: all 0.4s;
    -moz-transition: all 0.4s;
    transition: all 0.4s;
  }
  
  .discord-button-img{
    width: 20%;
    margin-right: 20px;
  }
  
  
  @media (max-width: 860px) {
    .content-left {
      display: none;
    }
  
    .content-right {
      max-width: inherit;
    }
  
    .content-right {
      border: none;
      width: 100%;
      padding-top: 1em;
      padding-bottom: 1em;
      padding-right: 1em;
      padding-left: 1em;
    }
  
    .logo {
      float: inherit;
      width: 10em;
    }
  
    .logo-right {
      display: inherit;
    }
    .hero .sh-text {
      font-size: 2.5em;
    }
    .hero .sh-bottomline {
      width: 11.5em !important;
      border-right: solid #1cc964 7.5em !important;
    }
    .front-para div {
    }
  }
  
  @media (max-width: 480px) {
    .header-item{
      padding: 0.5em;
    }
    .logo {
      width: 7em;
      padding-right: 1em;
    }
    .hero .sh-text {
      font-size: 2.2em;
    }
    .hero .sh-bottomline {
      width: 9.5em !important;
      height: 0.7em !important;
      border-right: solid #1cc964 7.3em !important;
    }
    .sub-header .sh-bottomline {
      width: 0em !important;
    }
    .hero .sh-topline {
      height: 0.7em;
      width: 100%;
      border-left: solid #1cc964 3.5em;
    }
    .rm-container {
      width: 75%;
    }
  }
  body{
    overflow-x: hidden;
  }